<template>
  <div class="formItem">
    <div style="text-align:right;">
      <el-button type="primary" icon="el-icon-edit" circle @click="handleEdit" v-if="userInfo.Suppliers_Status!=1"></el-button>
    </div>
    <el-form label-width="100px" :model="userInfo">
      <el-form-item label="供货店:">{{userInfo.Suppliers_Name}}</el-form-item>
      <el-form-item label="负责人:">{{userInfo.Suppliers_ResponPeople}}</el-form-item>
      <el-form-item label="供货商手机:">{{userInfo.Suppliers_MobilePhone}}</el-form-item>
      <el-form-item label="供货商地址:">{{userInfo.Suppliers_Addr}}</el-form-item>
        <el-form-item label="审核状态:">
        {{userInfo.Suppliers_Status | SupStatus}}
      </el-form-item>
      <el-form-item label="注册时间:">{{userInfo.CreateTime | filteTime}}</el-form-item>
      <el-form-item label="信息更新时间:">{{userInfo.UpdateTime |filteTime}}</el-form-item>
      <!-- <el-form-item label="最近登陆时间:">{{userInfo.Suppliers_LoginTime}}</el-form-item> -->

      <el-form-item label="供应商头像:" class="showImg" >
        <el-image style="width:200px; height: 200px" :src="isGetlocation+userInfo.Suppliers_HeadImage" fit="scale-down" v-if="userInfo.Suppliers_HeadImage"></el-image>
      </el-form-item>
      <el-form-item label="审核资料:" class="showImg">
        <el-image
          style="width:200px; height:200px"
          :src="isGetlocation+userInfo.Suppliers_ImgA"
          fit="scale-down"
          v-if="userInfo.Suppliers_ImgA"
        ></el-image>
        <el-image
          style="width:200px; height:200px"
          :src="isGetlocation+userInfo.Suppliers_ImgB"
          fit="scale-down"
           v-if="userInfo.Suppliers_ImgB"
        ></el-image>
        <el-image
          style="width:200px; height:200px"
          :src="isGetlocation+userInfo.Suppliers_ImgC"
           v-if="userInfo.Suppliers_ImgC"
          fit="scale-down"
        ></el-image>
        <el-image
          style="width:200px; height:200px"
          :src="isGetlocation+userInfo.Suppliers_ImgD"
          fit="scale-down"
           v-if="userInfo.Suppliers_ImgD"
        ></el-image>
        <el-image
          style="width:200px; height:200px"
          :src="isGetlocation+userInfo.Suppliers_ImgE"
          fit="scale-down"
           v-if="userInfo.Suppliers_ImgE"
        ></el-image>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  name: "HomeCenter",
  data() {
    return {
    }
  },
 created() {
  },
  computed: {
        isGetlocation(){
      return  this.$store.getters.isGetlocation;
    },
        userInfo(){
          return this.$store.getters.userInfoS;
        }
		},
  methods: {
      handleEdit(){
        this.$router.push({path:'basic'})
      }
  },
  filters: {
    filteTime(value){
      if(value&&value.indexOf('T')>-1){
        return value.replace(/T/g,' ');
      }else{
        return value;
      }
    },
    SupStatus(value) {
      switch(value) {
        case 0:
            return "未审核"
            break;
        case 1:
            return "已审核"
            break;
        case 2:
            return "已关闭"
            break;
        default:
            return ""
      }
    }
  }
};
</script>
<style scoped>
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.formItem{
  max-width:800px;
  padding:15px;
  margin:0 auto;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1)
}
.formItem form>div.el-form-item.showImg::before{
  content: "";
  display: block;
  clear: both;
}
.formItem form>div.el-form-item:not(.showImg){
  width:50%;
  float:left;
}
</style>



